import { Component, h, Prop } from '@stencil/core';
import { Notification } from './interfaces';

@Component({
  tag: 'salla-notification-item',
  styleUrl: 'salla-notifications.scss',
})
export class SallaNotificationItem {

  /**
   * Notification instance to render.
   */
  @Prop() notification: Notification;

  render() {
    return <a href={this.notification.url}
          class={{ "s-notifications-item": true, "s-notifications-item-read": !this.notification.is_new }}>
          <span class="s-notifications-item-icon">
            <i class={{ 's-notifications-item-icon sicon-bell-ring': true, 's-notifications-item-icon-new': this.notification.is_new }}></i>
          </span>
          <div class="s-notifications-item-content">
            <div class="s-notifications-item-content-leading">
              <h4>{this.notification.title}</h4>
              <p>{this.notification.sub_title}</p>
            </div>
            <span class="s-notifications-item-content-trailing">
              <i class="sicon-time s-notifications-item-content-trailing-icon"></i>
              <span>{this.notification.date}</span>
            </span>
          </div>
        </a>
  }
}
